<template>
  <transition name="modal">
    <div class="modal-mask" @mousedown="$emit('close')">
      <div class="modal-wrapper">
        <div class="modal-container" @mousedown.stop>
          <div class="modal-header">
            <slot name="header">
            </slot>
          </div>
          <div class="modal-body">
            <slot name="body">
             <VueDaumPostcode  @complete="result = $event" style="height: 400px; overflow: scroll;" />
            </slot>
          </div>
          <div class="modal-footer">
            <slot name="footer">
              <div>선택하신 주소:{{result.address}}</div>
              <button type="button" class="modal-default-button" @click="[test(),$emit('close')]"> 
                확인
              </button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { VueDaumPostcode } from "vue-daum-postcode"
export default {
  data(){
    return {
    result:'', 

    }
  },
  components:{
    VueDaumPostcode,
  },
  methods: {
    test(){
      
      this.$emit('raddress',this.result);
    

}
  
  },
}
</script>

<style>

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 500px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}
.sheader{
  display: felx;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>